//layout mixins - start

@mixin edgeTableLayout() {
    position: relative;
    display: table;
    table-layout: fixed;
    height: 100%;
    width: 100%;
}

@mixin edgeTableCellLayout($vertical-align: middle) {
    position: relative;
    display: table-cell;
    height: 100%;
    width: 100%;
    vertical-align: $vertical-align;
}

@mixin edgeRelativeHolderLayout($vertical-align: middle) {
    position: relative;
    display: inline-block;
    width: 100%;
    vertical-align: middle;
}

@mixin edgeAbsoluteHolderLayout() {
    position: absolute;
    display: block;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}

@mixin edgeTypographyLayout() {
    color: inherit;
    font-family: inherit;
    font-size: inherit;
    font-weight: inherit;
    font-style: inherit;
    line-height: inherit;
    letter-spacing: inherit;
    text-transform: inherit;
}

//layout mixins - end

//transition mixins - start

@mixin edgeTransition($transition-param...) {
    -webkit-transition: $transition-param;
    -moz-transition: $transition-param;
    transition: $transition-param;
}

@mixin edgeTransitionTransform($transition-param...) {
    -webkit-transition: -webkit-transform $transition-param;
    -moz-transition: -moz-transform $transition-param;
    transition: transform $transition-param;
}

@mixin edgeTransform($transform-param...) {
    -webkit-transform: $transform-param;
    -moz-transform: $transform-param;
    transform: $transform-param;
}

@mixin edgeAnimation($animation-param...) {
    -webkit-animation: $animation-param;
    -moz-animation: $animation-param;
    animation: $animation-param;
}

@mixin edgeTransformOrigin($animation-param...) {
    -webkit-transform-origin: $animation-param;
    -moz-transform-origin: $animation-param;
    transform-origin: $animation-param;
}

@mixin edgeBorderRadius($border-radius...) {
    -webkit-border-radius: $border-radius;
    -moz-border-radius: $border-radius;
    border-radius: $border-radius;
}

//transition mixins - end

//checkbox mixins - start

$checkbox-size: 15px;
$checkbox-border-width: 1px;
%checkbox-style {
    position: relative;
    margin: 8px 0;
    line-height: 1;
    input[type=checkbox] {
        width: $checkbox-size;
        height: $checkbox-size;
        max-height: $checkbox-size;
        position: relative;
        display: inline-block;
        vertical-align: top;
        top: 0;
        left: 0;
        margin: 0;
    }
    input[type=checkbox] + label {
        display: inline-block;
        pointer-events: none;
        cursor: pointer;;
        position: absolute;
        top: 0;
        left: 0;
        line-height: 0;
    }
    input[type=checkbox] + label span.edge-label-text {
        padding-left: 10px;
        display: inline-block;
        line-height: $checkbox-size;
        color: $default-heading-color;
    }
    input[type=checkbox] + label .edge-label-view {
        display: inline-block;
        width: $checkbox-size;
        height: $checkbox-size;
        box-sizing: border-box;
        vertical-align: top;
        cursor: pointer;
        border: $checkbox-border-width solid $default-border-color;
        background-color: $default-background-color;
        border-radius: 2px;
        &:hover {
            cursor: pointer;
        }
    }
    input[type=checkbox] + label .edge-label-view:after {
        content: '';
        width: $checkbox-size / 2 - $checkbox-border-width;
        height: $checkbox-size / 2 - $checkbox-border-width;
        position: absolute;
        top: ($checkbox-size - ($checkbox-size / 2 - $checkbox-border-width)) / 2;
        left: ($checkbox-size - ($checkbox-size / 2 - $checkbox-border-width)) / 2;
        background-color: $first-main-color;
        opacity: 0;
        @include edgeTransition(opacity 0.3s ease-in-out);
    }
    input[type=checkbox]:checked + label .edge-label-view:after {
        opacity: 1;
    }
}

//checkbox mixins - end

/* common mixins - start */

@mixin edgeBckImageStyle() {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
}

@mixin edgeImageOverlayHoverStyle($with-hover: true) {

    @if ($with-hover) {

        &:hover {

            &:after {
                opacity: .6;
            }
        }

        &:after {
            @include edgeAbsoluteHolderLayout();
            content: '';
            opacity: 0;
            @include edgeTransition(opacity .2s ease-in-out);
        }

    } @else {
        @include edgeAbsoluteHolderLayout();
        content: '';
        background-color: rgba($default-heading-color, .4);
        opacity: 0;
        @include edgeTransition(opacity .2s ease-in-out);
    }
}

@mixin edgeStandardPaginationStyle($list_type: null) {
    @include edgeRelativeHolderLayout(top);
    margin: 0 0 8px;
    clear: both;

    ul {
        @include edgeRelativeHolderLayout(top);
        padding: 0;
        margin: 0;
        list-style: none;

        li {
            position: relative;
            display: inline-block;
            vertical-align: top;
            margin: 0 8px;
            font-family: $default-text-font;
            font-weight: 700;
            line-height: 1em;

            a {
                position: relative;
                display: inline-block;
                vertical-align: top;
                margin: 0;
                padding: 0;
                color: $first-main-color;

                &:after {
                    content: '';
                    height: 2px;
                    position: relative;
                    display: block;
                    width: auto;
                    background-color: transparent;
                    @include edgeTransition(background-color .3s ease-in-out,width .3s ease-in-out);
                }

                &:hover {

                    &:after {
                        background-color: $first-main-color;
                    }
                }
            }

            &.edge-pag-active {

                a {
                    color: $first-main-color;

                    &:after {
                        content: '';
                        height: 2px;
                        position: relative;
                        display: block;
                        width: auto;
                        background-color: $first-main-color;
                    }
                }
            }

            &.edge-pag-prev,
            &.edge-pag-next,
            &.edge-pag-first,
            &.edge-pag-last {
                margin: 0 8px;

                a {
                    font-size: 24px;

                    &:after {
                        display: none;
                    }

                    span {
                        display: block;
                        line-height: inherit;

                        &:before {
                            display: block;
                            line-height: inherit;
                        }
                    }
                }
            }

            @if ($list_type == 'shortcode') {

                &.edge-pag-prev {

                    a {
                        opacity: 0;
                    }
                }

                &.edge-pag-next {

                    a {
                        opacity: 1;
                    }
                }

            } @else if ($list_type == 'shop') {
                span {
                    position: relative;
                    display: inline-block;
                    vertical-align: top;
                    margin: 0;
                    padding: 0;
                    color: $first-main-color;

                    &:not(.dots) {

                        &:after {
                            content: '';
                            height: 2px;
                            position: relative;
                            display: block;
                            width: auto;
                            background-color: $first-main-color;
                        }
                    }
                }

                a {

                    &.next,
                    &.prev {
                        font-size: 0;
                        line-height: 0;

                        &:before {
                            display: block;
                            font-family: 'ElegantIcons'; // same icon pack as in our templates for pagination
                            font-size: 24px;
                            line-height: 12px;
                            -webkit-font-smoothing: antialiased;
                            -moz-osx-font-smoothing: grayscale;
                        }

                        &:after {
                            display: none;
                        }
                    }

                    &.prev {
                        margin-right: -10px;

                        &:before {
                            content: "\34";
                        }
                    }

                    &.next {
                        margin-left: -10px;

                        &:before {
                            content: "\35";
                        }
                    }
                }
            }
        }
    }
}

@mixin edgeButtonDefaultStyle() {
    font-family: $default-heading-font;
    position: relative;
    display: inline-block;
    vertical-align: middle;
    width: auto;
    outline: none;
    font-size: 14px;
    line-height: 2em;
    font-weight: 500;
    text-transform: uppercase;
    box-sizing: border-box;
    margin: 0;
	border-radius: 0;
    @include edgeTransition(color .2s ease-in-out, background-color .2s ease-in-out, border-color .2s ease-in-out);
}

@mixin edgeButtonTransparentColor() {
    color: $default-text-color;
    background-color: transparent;
}

@mixin edgeButtonSolidColor() {
    color: #fff;
    background-color: $default-dark-color;
    border: 2px solid transparent;
}

@mixin edgeButtonSolidHoverColor() {
    color: #fff;
    background-color: $first-main-color;
}

@mixin edgeButtonOutlineColor() {
    color: $default-dark-color;
    background-color: transparent;
    border: 2px solid $default-dark-color;
}

@mixin edgeButtonOutlineHoverColor() {
    color: #fff;
    background-color: $default-dark-color;
    border-color: $default-dark-color;
}

@mixin edgeButtonSmallParams() {
    font-size: 14px;
    padding: 10px 21px;
}

@mixin edgeButtonMediumParams() {
    padding: 10px 34px;
}

@mixin edgeButtonLargeParams() {
    padding: 14px 45px;
}

@mixin edgeButtonHugeParams() {
    display: block;
    text-align: center;
    padding: 14px 51px;
}

@mixin edgePlaceholder {
    &::-webkit-input-placeholder {
        @content
    }
    &:-moz-placeholder {
        @content;
        opacity: 1;
    }
    &::-moz-placeholder {
        @content;
        opacity: 1;
    }
    &:-ms-input-placeholder {
        @content
    }
}

/* common mixins - end */

/* mixins styles - start */

%input-style {
    position: relative;
    width: 100%;
    margin: 0 0 48px;
    padding: 8px 5px 3px;
    font-family: inherit;
    font-size: 14px;
    line-height: 20px;
    font-weight: 400;
    color: $default-dark-color;
    background-color: transparent;
    border: 0;
    border-bottom: 1px solid $default-dark-color;
    border-radius: 0;
    outline: 0;
    -webkit-appearance: none;
    cursor: pointer;
    box-sizing: border-box;
    @include edgeTransition(border-color 0.2s ease-in-out);

    &:focus {
        color: $default-heading-color;
        border-color: $default-heading-color;
    }

    @include edgePlaceholder {
        color: inherit;
    }
}

/* mixins styles - end */

